.blur {
    filter: blur(5px);
    -webkit-filter: blur(5px);
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);

    animation: blurKeyframe 5s ease-out 0s 1;
    -webkit-animation: blurKeyframe 5s ease-out 0s 1;
    -moz-animation: blurKeyframe 5s ease-out 0s 1;
}

@keyframes blurKeyframe {

    from {
        -webkit-filter: blur(75px);
        -moz-filter: blur(75px);
        -o-filter: blur(75px);
        -ms-filter: blur(75px);
    }

    to {
        -webkit-filter: blur(5px);
        -moz-filter: blur(5px);
        -o-filter: blur(5px);
        -ms-filter: blur(5px);
    }

}

.disabledPointers {
    pointer-events: none;
}

.oswald {
    font-family: 'Oswald', sans-serif;
    font-size: 50px;
}

div img {
    max-height: 300px;
    width: auto;
}